<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";
import "element-ui/lib/theme-chalk/display.css";
import { FunctionalCalendar } from "vue-functional-calendar";

export default {
  name: "home",
  components: {
    HelloWorld,
    FunctionalCalendar
  },
  data() {
    return {
      activeNames: [1],
      ITcode: 123,
      form: {
        name: "",
        startDate: "",
        desc: "",
        startTime: "",
        teamName: ""
      },
      calendarData: {},
      drawer: false,
      allEvents: [
        {
          id: 1,
          name: "Amy",
          content: "hell01",
          due: "2019-29-12"
        },
        {
          id: 2,
          name: "Sara",
          content: "hello2",
          due: "2018-23-12"
        }
      ]
    };
  },
  computed: {
    auth() {
      if (this.ITcode === 123) {
        return false;
      } else {
        return true;
      }
    },
    selectedDate() {
      return this.calendarData.selectedDate;
    }
  },
  methods: {
    onSubmit() {
      let obj = {
        'due': this.form.startDate,
        'content': this.form.desc,
        'team': this.form.teamName,
        'who': this.form.name,
        'time': this.form.startTime
      };
      console.log(obj);
      this.$http.post("/api/contract.php", obj, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(res => {
        console.log(res);
      })
    },
    clearInput() {
      this.form.name = "";
      this.form.startDate = "";
      this.form.desc = "";
      this.form.startTime = "";
      this.form.teamName = "";
    }
  },
  watch: {
    // calendarData: {
    //   handler(val) {
    //     console.log(val);
    //   },
    //   deep: true
    // }
    selectedDate(val) {
      // 改变date format：若月份和日期是个位数，则加入前导零
      let temp = val.split("-");
      for (let i = 1; i < temp.length; ++i) {
        if (temp[i].length == 1) {
          temp[i] = "0" + temp[i];
        }
      }
      let fval = temp[0] + "-" + temp[1] + "-" + temp[2];
      this.$http.get("/api/contract.php" + "?" + "due=" + fval).then(res => {
        console.log(res);
        this.allEvents = res.data;
      });
    }
  }
};
</script>


